@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro:ital,wght@0,200;0,300;0,400;0,500;0,600;1,200;1,300;1,400;1,500;1,600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:ital,wght@0,200;0,300;0,400;0,600;0,700;0,900;1,200;1,300;1,400;1,600;1,700;1,900&display=swap);
:root {
  --sg-blurple: #5033e1;
  --sg-light-gray: #f4f7fb;
  --sg-vermillion: #ff5543;
  --sg-violet: #a112ff;
  --sg-mint: #8fedcf;
  --sg-black: #000000;
  --sg-btn-hover: #3c26a5;
  --sg-dark-gray: #6C757D;
  --sg-bg-dark-1: #181b26;
  --sg-bg-dark-2: #1d212f;
  --sg-bg-dark-3: #343a4d;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.btn {
  border: none;
  border-radius: 3px;
  background-color: #5033e1;
  color: #ffffff;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  min-width: -webkit-fit-content;
  min-width: -moz-fit-content;
  min-width: fit-content;
  height: 40px;
  font-family: "Source Sans Pro", Arial, Helvetica, sans-serif;
  font-weight: normal;
  font-size: 1.0rem;
  margin-top: 10px;
  padding-right: 22px;
  padding-left: 22px;
  justify-self: flex-end;
  transition: all .2s ease-in;
  cursor: pointer;
}

.btn:hover {
  background-color: #3c26a5;
  background-color: var(--sg-btn-hover);
  color: white;
}

.btn:active {
  background-color: #ffffff;
  color: #5033e1;
}

button.btn:disabled,
button.btn[disabled] {
  border-color: #f4f7fb;
  border-color: var(--sg-light-gray);
  background-color: #f4f7fb;
  background-color: var(--sg-light-gray);
  color: lightgray;
  font-weight: regular;
  cursor: default;
}

.btn.dark {
  border: none;
  border-radius: 3px;
  background-color: #5033e1;
  background-color: var(--sg-blurple);
  color: #ffffff;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  min-width: -webkit-fit-content;
  min-width: -moz-fit-content;
  min-width: fit-content;
  height: 40px;
  font-family: "Source Sans Pro", Arial, Helvetica, sans-serif;
  font-weight: normal;
  font-size: 1.0rem;
  margin-top: 10px;
  padding-right: 22px;
  padding-left: 22px;
  justify-self: flex-end;
  transition: all .2s ease-in;
  cursor: pointer;
}

.btn.dark:hover {
  background-color: #3c26a5;
  background-color: var(--sg-btn-hover);
  color: white;
}

.btn.dark:active {
  background-color: #2b1b79;
  color: white;
}

button.btn.dark:disabled,
button.btn.dark[disabled] {
  border-color: #1d212f;
  border-color: var(--sg-bg-dark-2);
  background-color: #1d212f;
  background-color: var(--sg-bg-dark-2);
  color: #343a4d;
  color: var(--sg-bg-dark-3);
  font-weight: regular;
  cursor: default;
}
.deployment-container {
    width: 100%;
    text-align: left;
    max-width: 325px;
}

.deployment-type {
    width: 100%;
}

.dropdown-menu {
    border: 1px solid lightgray;
    border-radius: 4px;
    font-family: "Source Sans Pro", monospace;
    font-size: 1rem;
    color: rgb(47, 47, 47);
    outline-color: var(--sg-blurple);
    padding: 5px;
    height: 38px;
    width: 100%;
    max-width: 100%;
    min-width: -webkit-fit-content;
    min-width: -moz-fit-content;
    min-width: fit-content;
}

.ns-check.dark {
    background-color: var(--sg-bg-dark-3);
    border: 1px solid var(--sg-dark-gray);
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    width: 12px;
    height: 12px;
    border-radius: 2px;
}

.ns-check.dark:checked:after {
    content: '\2713';
    font-size: 14px;
    color: white;
    position: relative;
    top: -2.5px;
}

.dropdown-menu.dark {
    background-color: var(--sg-bg-dark-2);
    border: 1px solid var(--sg-dark-gray);
    outline-color: var(--sg-dark-gray);
    color: white;
}

.text-input {
    border: 1px solid lightgray;
    border-radius: 4px;
    font-family: "Source Code Pro", monospace;
    font-size: 1rem;
    color: rgb(47, 47, 47);
    outline-color: var(--sg-blurple);
    padding-left: 8px;
    margin-top: 3px;
    height: 32px;
    width: 97%;
}

.text-input.dark {
    background-color: var(--sg-bg-dark-2);
    border: 1px solid var(--sg-dark-gray);
    outline-color: var(--sg-dark-gray);
    color: white;
}

.namespace-check {
    margin-top: 10px;
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    font-family: "Source Sans Pro", Arial, Helvetica, sans-serif;
    font-size: .8rem;
}

.namespace-label {
    margin-left: 4px;
    min-width: -webkit-fit-content;
    min-width: -moz-fit-content;
    min-width: fit-content;
}

.namespace-label.dark {
    color: white;
}

.namespace-input {
    margin-top: 10px;
    width: 100%;
}
.actions-container {
    padding-top: 10px;
    width: 100%;
    max-width: 325px;
    min-width: 150px;
    text-align: left;
}

.action-option {
    margin-top: 10px;
    width: 100%;
}
.desc-list {
    list-style: none;
    text-align: left;
    font-family: "Source Code Pro", monospace;
    font-size: .7rem;
}

.desc-item {
    display: flex;
    flex-flow: column nowrap;
}

.desc-item.dark {
    color: white;
}

.desc-list.placeholder {
    font-size: .8rem;
}

.desc-list.placeholder.dark {
    color: white;
}

.description {
    list-style: none;
    padding-right: 5px;
}

.description.dark {
    background-color: var(--sg-bg-dark-3);
}

.instruction {
    font-weight: normal;
    font-style: italic;
    font-size: .8rem;
}

.row {
    padding-top: 2px;
    padding-bottom: 2px;
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: space-between;
    font-size: .8rem;
    cursor: pointer;
    border-radius: 2px;
    padding-left: 5px;
    padding-right: 5px;
    max-width: 340px;
    /* border: 1px solid black; */
}

.row:hover {
    background-color: var(--sg-light-gray);
}

.row.dark:hover {
    background-color: var(--sg-bg-dark-3);
}

.row.expanded {
    background-color: var(--sg-light-gray);
}

.row.expanded.dark {
    background-color: var(--sg-bg-dark-3);
}

.expand {
    border: none;
    background-color: transparent;
    font-family: "Source Code Pro", monospace;
    font-size: .7rem;
    cursor: pointer;
    transition: .2s;
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    position: relative;
    left: 40px;
    opacity: 0;
    transition: all .3s ease-in-out;
}

.expand.dark {
    color: white;
}

.expand.active {
    opacity: 1;
    transform: translateX(-40px);
}

.info-key {
    font-weight: bold;
}

.rotated {
    transform: rotate(-90deg);
    transition-duration: .2s;
}

.not-rotated {
    transition-duration: .2s;
}

.command-info {
    background-color: var(--sg-light-gray);
    font-family: "Source Code Pro", Arial, Helvetica, sans-serif;
    font-size: .7rem;
    padding-bottom: 10px;
    width: 100%;
    max-width: 350px;
    animation: fadeIN .2s linear;
    opacity: 1;
}

.command-info.dark {
    background-color: var(--sg-bg-dark-3);
    color: white;
}

@keyframes fadeIN {

    0% {
        opacity: 0;
        -webkit-transform: translateY(-15px);
    }

    100% {
        opacity: 1;
        -webkit-transform: translateY(0);
    }
}
button.copy-button:disabled,
button.copy-button[disabled] {
    border: 2px solid #F4F7Fb;
    border-radius: 3px;
    background-color: #ffffff;
    color: lightgray;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: 40px;
    font-family: "Source Sans Pro", Arial, Helvetica, sans-serif;
    font-weight: regular;
    font-size: 1.0rem;
    padding-right: 22px;
    padding-left: 22px;
}

.copy-button {
    border: 2px solid #5033e1;
    border-radius: 3px;
    background-color: #ffffff;
    color: #5033e1;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: 40px;
    font-family: "Source Sans Pro", Arial, Helvetica, sans-serif;
    font-weight: 600;
    font-size: 1.0rem;
    padding-right: 22px;
    padding-left: 22px;
}

.copy-button:hover {
    background-color: #5033e1;
    color: white;
    cursor: pointer;
    padding-right: 22px;
    padding-left: 22px;
}
.generated-command-container {
    padding-top: 45px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width: 100%;
    min-height: 100px;
    max-width: 800px;
    min-width: 550px;
    margin: auto;
}

.generated-command {
    font-family: "Source Code Pro", monospace;
    font-weight: normal;
    font-size: .9rem;
    background-color: #000000;
    padding-top: 25px;
    padding-left: 20px;
    padding-right: 20px;
    padding-bottom: 25px;
    color: var(--sg-mint);
    border-radius: 3px;
}

.generated-command.dark {
    background-color: var(--sg-bg-dark-3);
    color: #ffffff;
}

.copied-message.show {
    color: #ED2E20;
    font-family: "Source Sans Pro", Arial, Helvetica, sans-serif;
    font-size: 1.0;
    font-weight: light;
    animation-name: fadeIn;
    animation-duration: .4s;
    text-align: left;
    transition-duration: .4s;
}

.copied-message.dark {
    color: var(--sg-mint);
}

.btns-container {
    width: 100%;
    max-width: 800px;
    min-width: 550px;
    margin: auto;
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
}

.btns {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    width: 36%;
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}
.App {
  text-align: center;
  background-color: #ffffff;
  height: 100vh;
}

.App.dark {
  background-color: var(--sg-bg-dark-1);
}

.container {
  width: 65%;
  max-width: 1020px;
  margin: auto;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  justify-content: center;
}

.deploy-container {
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;
  align-items: flex-end;
  width: 45%;
  min-width: 320px;
  min-height: 450px;
  max-height: 500px;
  margin-left: auto;
  margin-right: 0px;
}

.line-break {
  height: 400px;
  min-height: 400px;
  border-left: 1px solid lightgray;
  margin-left: 30px;
  margin-right: 30px;
}

.descriptions-container {
  width: 50%;
  min-width: 320px;
  min-height: 450px;
  max-height: 500px;
}

.logo {
  width: 300px;
  margin-top: 100px;
}

.subtitle {
  font-family: "Source Sans Pro", monospace;
  font-style: italic;
  font-weight: 400;
  font-size: 1.0rem;
  color: var(--sg-dark-gray);
}

.subtitle.dark {
  color: white;
}

.section {
  font-family: "Source Sans Pro", Arial, Helvetica, sans-serif;
  font-weight: 600;
  text-align: left;
}

.section.right {
  text-align: right;
}

.section.dark {
  color: white;
}
